define('frontend-cp/tests/acceptance/showcase/render-test', ['frontend-cp/tests/helpers/qunit'], function (qunit) {

  'use strict';

  qunit.app('Acceptance | Showcase| Render', {
    beforeEach: function beforeEach() {
      login();
    },

    afterEach: function afterEach() {
      logout();
    }
  });

  qunit.test('no errors occur', function (assert) {
    assert.expect(1);

    visit('/showcase');

    andThen(function () {
      assert.equal(currentURL(), '/showcase');
    });
  });

});